.label {
  display: flex;
  fill: orangered;
  align-items: center;

  &__svg {
    display: flex;
    justify-content: start;
    align-items: flex-start;
    width: 50px;
    height: 50px;
  }

  &__path {
    transform: scale(9.8%);
  }

  &__subtitle {
    text-transform: uppercase;
    font-size: 18px;
  }
}
